import { registerBlockType } from '@wordpress/blocks';

import couponShortcode from  'blocks/shortcode';
import './style.pcss';

const blocks = [
	couponShortcode,
];

blocks.forEach( block => {
	const blockName = `pngx/${ block.id }`;
	registerBlockType( blockName, block );
} );

export default blocks;


